           // ‚ö°‚ö°‚ö° Á≠âÂæÖ Approve Á°ÆËÆ§
           console.log('[Swap] ‚ö°‚ö°‚ö° Waiting for Approve confirmation...')
           const approveConfirmStart = Date.now()
           await publicClient.waitForTransactionReceipt({
               hash: approveTxHash,
               confirmations: 0,
               timeout: 15_000,
               pollingInterval: 500
           })
           console.log(`[Swap] ‚ö° Approve confirmed (${Date.now() - approveConfirmStart}ms)`)
           
          // ‚ö°‚ö°‚ö° ‰ºòÂåñË∑ØÁî±ÔºàRADRS Á®éÊî∂‰ª£Â∏ÅÈúÄË¶ÅÊõ¥È´ò GasÔºâ
          const isUsdtRadrs = (fromAsset.contractAddress === USDT_ADDRESS && toAsset.contractAddress === RADRS_ADDRESS) ||
                              (fromAsset.contractAddress === RADRS_ADDRESS && toAsset.contractAddress === USDT_ADDRESS)
